.side-nav-wrapper {
  width: 280px;
  background-color: #051c2c;
  color: white;
  position: fixed;
  top: 90px;
  height: calc(100vh - 90px);
  left: -280px;
  -webkit-transition: left 0.3s ease-out;
  -moz-transition: left 0.3s ease-out;
  -ms-transition: left 0.3s ease-out;
  -o-transition: left 0.3s ease-out;
  transition: left 0.3s ease-out;
  z-index: 9;
}
@media (max-width: 1024px) {
  .side-nav-wrapper {
    border-top: 1px solid rgba(192, 192, 192, 0.3);
  }
}
.side-nav-wrapper .side-nav-scroll {
  overflow-y: auto;
  height: calc(100vh - 118px);
}
.side-nav-wrapper .side-nav-scroll .os-scrollbar > .os-scrollbar-track > .os-scrollbar-handle {
  background: #C4C4C4;
}
.side-nav-wrapper .menu-list li {
  position: relative;
}
.side-nav-wrapper .menu-list li .nav-item {
  padding: 15px;
}
span .side-nav-wrapper .menu-list li .nav-item:first-child {
  padding-right: 50px;
  display: block;
}
.side-nav-wrapper .menu-list li .nav-item .rt-prgs {
  float: right;
}
.side-nav-wrapper .menu-list li .nav-item .rt-prgs .plus-icon {
  margin-left: 5px;
  display: inline-block;
  width: 10px;
  height: 10px;
  position: relative;
}
.side-nav-wrapper .menu-list li .nav-item .rt-prgs .plus-icon:after {
  content: '\002B';
  color: #fff;
  font-size: 1.25em;
  font-weight: bold;
}
.side-nav-wrapper .menu-list li .nav-item.open .rt-prgs .plus-icon:after {
  content: "\2212";
  font-size: 1.25em;
}

.wrapper.toggled .inner-container .side-nav-wrapper {
  left: 0;
}

.submenu-list {
  list-style: none;
  max-height: 0;
  overflow: hidden;
  background: #04141f;
}
.submenu-list .sub-links {
  border-bottom: 1px solid #0e2230;
}
.submenu-list .sub-links:last-child {
  border-bottom: none;
}
.submenu-list .sub-links .nav-item {
  font-size: 0.85em !important;
  padding-left: 30px !important;
}
.submenu-list .sub-links.selected {
  color: #2251FF;
  border-left: 5px solid transparent;
  background-color: #0c2e47;
}

.side-nav-wrapper .menu-list li .nav-item {
  padding: 20px 15px;
  font-size: 0.85rem;
  font-weight: normal;
  border-left: 5px solid transparent;
  cursor: pointer;
}
.side-nav-wrapper .menu-list li .nav-item.selected {
  color: #2251FF;
  border-color: #2251FF;
  background-color: #082336;
}
@media (max-width: 767px) {
  .side-nav-wrapper .menu-list li .nav-item {
    font-size: 0.80em;
  }
}
.side-nav-wrapper .menu-list li .nav-item > span:first-child {
  display: inline-block;
  width: calc(100% - 26px);
}

.menuAnimate {
  -webkit-transition: max-height 0.4s linear;
  -moz-transition: max-height 0.4s linear;
  -ms-transition: max-height 0.4s linear;
  -o-transition: max-height 0.4s linear;
  transition: max-height 0.4s linear;
}

.open {
  max-height: 500px;
